<template>
  <div class="add_teacher">
    <h1></h1>
    <Form
      ref="AddTeacher"
      :model="teacherInfo"
      :rules="teacherRule"
      :label-width="100"
      label-position="left"
    >
      <div class="teacher_input">
        <div class="input_left">
          <FormItem label="" prop="TID">
            <Input v-model="teacherInfo.TID" placeholder="" />
          </FormItem>
          <FormItem label="" prop="TName">
            <Input v-model="teacherInfo.TName" placeholder="" />
          </FormItem>
          <FormItem label="" prop="GuideProfession">
            <Select @on-change="guideProfessionChange">
              <Option v-for="profession in professions" :value="profession" :key="profession"></Option>
            </Select>
          </FormItem>
          <FormItem label="" prop="Password">
            <Input v-model="teacherInfo.Password" placeholder="" />
          </FormItem>
        </div>
        <div class="input_right">
          <FormItem label="" prop="Position">
            <Input v-model="teacherInfo.Position" placeholder="" />
          </FormItem>
          <FormItem label="" prop="Rank">
             <Select @on-change="RankChange">
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </FormItem>
          <FormItem label="" prop="Phone">
            <Input v-model="teacherInfo.Phone" placeholder="" />
          </FormItem>
          <FormItem label="" prop="Email">
            <Input v-model="teacherInfo.Email" placeholder="" type="email" />
          </FormItem>
        </div>
      </div>
      <div style="text-align:center">
        <Button
          type="success"
          @click="addTeacher('AddTeacher')"
          style="margin-top:30px;width:300px"
        ></Button>
      </div>
    </Form>
  </div>
</template>

<script>
export default {
  name: "AddTeacher",
  data() {
    return {
      teacherInfo: {
        TID: "",
        TName: "",
        Position: "",
        Rank: "",
        GuideProfession: "",
        Phone: "",
        Email: "",
        Password: "123456"
      },
      teacherRule: {
        TID: [{ required: true, message: "", trigger: "blur" }],
        TName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: "2-10",
            trigger: "blur"
          }
        ],
        GuideProfession: [
          { required: true, message: "", trigger: "blur" }
        ],
        Password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 30,
            message: "6-30",
            trigger: "blur"
          }
        ]
      },
      professions: ["", ""]
    };
  },
  methods: {
    addTeacher: function(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("");
        } else {
          this.$Message.error("");
        }
      });
    },
    guideProfessionChange: function(value) {
      this.teacherInfo.GuideProfession = value;
    },
    RankChange: function(value){
      this.teacherInfo.Rank = value;
    }
  }
};
</script>

<style scoped>
.add_teacher {
  width: 800px;
  margin: 30px auto;
}
.add_teacher h1 {
  text-align: center;
  margin-bottom: 30px;
}
.teacher_input {
  display: flex;
}
.input_left,
.input_right {
  width: 300px;
  margin: 0 50px;
}
.ivu-form-item-content {
  margin-left: 0 !important;
}
</style>