<template>
  <div v-if="topicInfo==null">
    <h1></h1>
  </div>
  <div v-else class="topic">
    <h1 style="color:#19be6b"></h1>
    <div class="info">
      
      <span class="content">{{topicInfo.TopicName}}</span>
    </div>
    <div class="info">
      
      <span class="content">{{topicInfo.Type}}</span>
    </div>
    <div class="info">
      
      <span class="content">{{topicInfo.TName}}</span>
    </div>
    <div class="info">
      
      <Card class="info intro">
        <p>{{topicInfo.Introduction}}</p>
      </Card>
    </div>
  </div>
</template>

<script>
export default {
  name: "MyTopic",
  data() {
    return {
      topicInfo: {}
    };
  },
  created() {
    this.topicInfo = {
      TopicName: "",
      TName: "",
      Type: "",
      Introduction:
        "(External Schema): , (External Schema)(Subschema), , (), , , "
    };
  }
};
</script>

<style scoped>
.topic {
  font-weight: bold;
  font-size: 16px;
  width: 600px;
  margin: auto;
}
.info {
  margin-top: 30px;
}
.intro {
  padding: 10px;
  font-size: 16px;
  line-height: 30px;
}
.content {
  display: inline-block;
  width: 500px;
  text-align: center;
}
h1 {
  text-align: center;
  margin: 30px auto 50px;
}
</style>