<template>
  <div>
    <List>
      <ListItem class="Padding10">{{topicList.length}}/20</ListItem>
      <ListItem style="background:#f8f8f9">
        <Row class="row">
          <Col span="5"></Col>
          <Col span="5"></Col>
          <Col span="5"></Col>
          <Col span="5"></Col>
          <Col span="4"></Col>
        </Row>
      </ListItem>
      <ListItem v-for="(topic,index) in topicList" :key="index">
        <Row class="row">
          <Col span="5">{{topic.TopicID}}</Col>
          <Col span="5">{{topic.TopicName}}</Col>
          <Col span="5">{{topic.Type}}</Col>
          <Col span="5">{{topic.SName?topic.SName:""}}</Col>
          <Col span="4">
            <Button type="primary" size="small" class="opButton" @click="changeTopic(index)"></Button>
            <Button
              type="error"
              size="small"
              class="opButton"
              @click="deleteTopic(topic.TopicID)"
            ></Button>
          </Col>
        </Row>
      </ListItem>
    </List>
    <Modal
      v-model="showInfoModal"
      :title="''+topicList[selectTopic].TopicID"
      class-name="vertical-center-modal"
      @on-ok="changeOK"
      @on-cancel="changeCancel"
    >
      <div style="width:400px;margin:30px auto">
        <Select v-model="selectKey" placeholder="" @on-change="keyChange">
          <Option
            v-for="(value,key) in topicList[selectTopic]"
            :value="key"
            :key="key"
          >{{keyList[key]}}</Option>
        </Select>
        <Input v-model="newValue" placeholder="" class="Top30"></Input>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "TopicManage",
  data() {
    return {
      selectTopic: 0,
      selectKey: "",
      showInfoModal: false,
      newValue: "",
      keyList: {
        TopicName: "",
        Introduction: "",
        Type: ""
      },
      topicList: []
    };
  },
  created() {
    this.topicList = [
      {
        TopicID: 1003,
        TopicName: "1",
        Introduction: "",
        Type: "",
        SName: null
      },
      {
        TopicID: 1004,
        TopicName: "",
        Introduction: "",
        Type: "",
        SName: "ARUKI"
      }
    ];
  },
  methods: {
    changeTopic: function(index) {
      this.selectTopic = index;
      this.showInfoModal = true;
    },
    deleteTopic: function(TopicID) {
      if (this.$Modal.confirm({ title: "" + TopicID })) {
        this.$Message.success("");
      }
    },
    keyChange: function(key) {
      this.newValue = this.topicList[this.selectTopic][key];
    },
    changeOK: function() {
      this.$Message.success("");
      this.changeCancel();
    },
    changeCancel: function() {
      this.selectTopic = 0;
      this.selectKey = "";
      this.newValue = "";
    }
  }
};
</script>

<style scoped>
.Padding10 {
  padding-left: 15px;
  padding-right: 15px;
}
.row {
  width: 100%;
  text-align: center;
}
.opButton {
  font-size: 12px;
  margin: auto 5px;
}
.Top30 {
  margin-top: 30px;
}
</style>