<template>
  <div class="wrap">
    <h1></h1>
    <Form
      ref="ApplyForm"
      :model="applyForm"
      :rules="ApplyRule"
      label-position="left"
      :label-width="100"
    >
      <FormItem label="" prop="TID">
        <Select v-model="applyForm.TID" placeholder="">
          <Option v-for="(teacher,index) in teachers" :value="teacher.TID" :key="index" @click.native="teacherChange(index)">{{teacher.TName}}</Option>
        </Select>
      </FormItem>
      <div v-show="showDemand" class="demandWrap">
        <strong></strong>
        <Card class="demand">{{demand}}</Card>
      </div>
      <FormItem label="" prop="TopicName">
        <Input v-model="applyForm.TopicName" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="Type">
        <Select v-model="applyForm.Type" @on-change="typeChange">
          <Option v-for="(value,key) in type" :value="key" :key="key">{{value}}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="Introduction">
        <Input
          v-model="applyForm.Introduction"
          type="textarea"
          :autosize="{minRows:6}"
          placeholder="3-800"
        ></Input>
      </FormItem>
      <div style="margin:30px auto;width:300px">
        <Button style="margin:30px auto;" type="primary" long @click="applySubmit('ApplyForm')"></Button>
      </div>
    </Form>
  </div>
</template>

<script>
export default {
  name: "StudentTopicApply",
  data() {
    return {
      teachers: [],
      type: {},
      demand: "",
      showDemand: false,
      applyForm: {
        TopicName: "",
        Type: "",
        Introduction: "",
        TID: ""
      },
      ApplyRule: {
        TopicName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 30,
            message: "3-30",
            trigger: "blur"
          }
        ],
        Type: [
          { required: true, message: "", trigger: "change" }
        ],
        Introduction: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 800,
            message: "3-800",
            trigger: "blur"
          }
        ],
        TID: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },
  created() {
    this.teachers = [
      { TID: "10001", TName: "teacher1", TopicDemand: "" },
      {
        TID: "10002",
        TName: "teacher2",
        TopicDemand:
          ", , , "
      },
      {
        TID: "10003",
        TName: "teacher3",
        TopicDemand:
          "NginxWebNginxNginxNginx"
      }
    ];
    this.type = {
      10001: "",
      10002: "",
      10003: "",
      10004: "",
      10005: ""
    };
  },
  methods: {
    teacherChange: function(index) {
      this.applyForm.TID = this.teachers[index].TID;
      this.demand = this.teachers[index].TopicDemand;
      this.showDemand = true;
    },
    typeChange: function(key) {
      this.applyForm.Type = key;
    },
    applySubmit: function(name) {
      this.$refs[name].validate((valid,error) => {
        if (valid) {
          this.$Message.success("");
        }
        if(error){
          console.log(error)
        }
      });
    }
  }
};
</script>

<style scoped>
h1 {
  text-align: center;
  margin: 30px auto 50px;
}
.wrap {
  margin: 30px auto;
  width: 500px;
}
.demandWrap {
  margin: 10px auto 50px;
}

.demand {
  margin: 20px;
  padding: 10px;
}
</style>