<template>
  <div>
    <List class="Padding20">
      <ListItem style="background:#f8f8f9">
        <Row class="row">
          <Col span="5"></Col>
          <Col span="4"></Col>
          <Col span="4"></Col>
          <Col span="5"></Col>
          <Col span="6"></Col>
        </Row>
      </ListItem>
      <ListItem v-for="(info,index) in applicationInfo" :key="index">
        <Row class="row">
          <Col span="5">{{info.topicInfo.TopicName}}</Col>
          <Col span="4">{{applyType[info.topicInfo.Source]}}</Col>
          <Col span="4">{{info.studentInfo.SName}}</Col>
          <Col span="5">{{info.ApplyTime}}</Col>
          <Col span="6">
            <Button type="info" size="small" class="opButton" @click="showInfo(index)"></Button>
            <Button
              type="success"
              size="small"
              class="opButton"
              @click="accept(info.studentInfo.SID, info.topicInfo.TopicID, info.studentInfo.SName, info.topicInfo.TopicName)"
            ></Button>
            <Button
              type="error"
              size="small"
              class="opButton"
              @click="refuse(info.studentInfo.SID, info.topicInfo.TopicID, info.studentInfo.SName, info.topicInfo.TopicName)"
            ></Button>
          </Col>
        </Row>
      </ListItem>
    </List>
    <Modal v-model="showInfoModal" class-name="vertical-center-modal" width="40%">
      <div class="Padding20">
        <Tabs type="card" v-model="tabStatus">
          <TabPane label="" name="0">
            <div class="Padding10">{{applicationInfo[index].topicInfo.TopicID}}</div>
            <div class="Padding10">{{applicationInfo[index].topicInfo.TopicName}}</div>
            <div class="Padding10">{{applicationInfo[index].topicInfo.Type}}</div>
            <div class="Padding10">{{applyType[applicationInfo[index].topicInfo.Source]}}</div>
            <div class="Padding10">
              
              <div class="Padding20">{{applicationInfo[index].topicInfo.Introduction}}</div>
            </div>
          </TabPane>
          <TabPane label="" name="1">
            <div class="Padding10">{{applicationInfo[index].studentInfo.SID}}</div>
            <div class="Padding10">{{applicationInfo[index].studentInfo.SName}}</div>
            <div class="Padding10">{{applicationInfo[index].studentInfo.ClassName}}</div>
          </TabPane>
        </Tabs>
      </div>
      <div slot="footer">
        <Button type="primary" long @click="closeInfo"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "ApplyManage",
  data() {
    return {
      showInfoModal: false,
      tabStatus: "0",
      index: 0,
      applyType: {
        0: "",
        1: ""
      },
      applicationInfo: []
    };
  },
  created() {
    this.applicationInfo = [
      {
        topicInfo: {
          TopicID: "10003",
          TopicName: "",
          Type: "",
          Introduction: "",
          Source: 0
        },
        studentInfo: {
          SID: "201835020822",
          SName: "ARUKI",
          ClassName: "8"
        },
        ApplyTime: "2020-5-25 1:05"
      }
    ];
  },
  methods: {
    showInfo: function(index) {
      this.index = index;
      this.showInfoModal = true;
    },
    closeInfo: function() {
      this.index = 0;
      this.tabStatus = "0";
      this.showInfoModal = false;
    },
    accept: function(SID, TopicID, SName, TopicName) {
      this.$Modal.confirm({
        title: "" + SName + "" + TopicName + "?"
      });
    },
    refuse: function(SID, TopicID, SName, TopicName) {
      this.$Modal.confirm({
        title: "" + SName + "" + TopicName + "?"
      });
    }
  }
};
</script>

<style scoped>
.row {
  width: 100%;
  text-align: center;
}
.Padding20 {
  padding: 20px;
}
.opButton {
  margin: auto 5px;
  font-size: 12px;
}
.Padding10 {
  padding: 10px;
}
</style>