<template>
  <div class="wrap">
    <Tabs size="small" v-model="tabstatus">
      <TabPane label="" name="Student">
        <List>
          <ListItem class="row-wrap" style="background:#f8f8f9">
            <Row class="row">
              <Col span="5"></Col>
              <Col span="3"></Col>
              <Col span="3"></Col>
              <Col span="5"></Col>
              <Col span="5"></Col>
              <Col span="3"></Col>
            </Row>
          </ListItem>
          <ListItem class="row-wrap" v-for="(student,index) in studentList" :key="index">
            <Row class="row">
              <Col span="5">{{student.SID}}</Col>
              <Col span="3">{{student.SName}}</Col>
              <Col span="3">{{student.Grade}}</Col>
              <Col span="5">{{student.Department}}</Col>
              <Col span="5">{{student.Profession}}</Col>
              <Col span="3">
                <Button
                  type="primary"
                  size="small"
                  style="margin-right:10px"
                  @click="changeStudent(index)"
                ></Button>
                <Button type="error" size="small" @click="deleteStudent(student.SID)"></Button>
              </Col>
            </Row>
          </ListItem>
        </List>
      </TabPane>
      <TabPane label="" name="Teacher">
        <List>
          <ListItem class="row-wrap" style="background:#f8f8f9">
            <Row class="row">
              <Col span="5"></Col>
              <Col span="3"></Col>
              <Col span="4"></Col>
              <Col span="4"></Col>
              <Col span="5"></Col>
              <Col span="3"></Col>
            </Row>
          </ListItem>
          <ListItem class="row-wrap" v-for="(teacher,index) in teacherList" :key="index">
            <Row class="row">
              <Col span="5">{{teacher.TID}}</Col>
              <Col span="3">{{teacher.TName}}</Col>
              <Col span="4">{{teacher.Position}}</Col>
              <Col span="4">{{teacher.Rank}}</Col>
              <Col span="5">{{teacher.GuideProfession}}</Col>
              <Col span="3">
                <Button
                  type="primary"
                  size="small"
                  style="margin-right:10px"
                  @click="changeTeacher(index)"
                ></Button>
                <Button type="error" size="small" @click="deleteTeacher(teacher.TID)"></Button>
              </Col>
            </Row>
          </ListItem>
        </List>
      </TabPane>
    </Tabs>
    <Modal
      v-model="showInfoModal"
      class-name="vertical-center-modal"
      @on-ok="changeOK"
      @on-cancel="changeCancel"
    >
      <div style="width:300px;margin:40px auto">
        <div v-if="tabstatus=='Student'">
          <h2>{{studentList[paneSelect].SID}}</h2>
          <Select v-model="selectKey" placeholder="" @on-change="keyChange">
            <Option
              v-for="(value,key) in studentList[paneSelect]"
              :value="key"
              :key="key"
            >{{keyList[key]}}</Option>
          </Select>
          <Input v-model="newValue" placeholder="" class="Top30" />
        </div>
        <div v-if="tabstatus=='Teacher'">
          <h2>{{teacherList[paneSelect].TID}}</h2>
          <Select v-model="selectKey" placeholder="" @on-change="keyChange">
            <Option
              v-for="(value,key) in teacherList[paneSelect]"
              :value="key"
              :key="key"
            >{{keyList[key]}}</Option>
          </Select>

          <Input
            v-model="newValue"
            placeholder=""
            class="Top30"
          />
        </div>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "AccountManage",
  data() {
    return {
      showInfoModal: false,
      tabstatus: "Student",
      paneSelect: 0,
      selectKey: "",
      newValue: "",
      studentList: [
        {
          SID: "201835020822",
          SName: "",
          Department: "",
          Profession: "",
          Grade: 2018,
          ClassNumber: 8,
          Password: "123456"
        },
        {
          SID: "201835020822",
          SName: "",
          Department: "",
          Profession: "",
          Grade: 2018,
          ClassNumber: 8,
          Password: "123456"
        }
      ],
      teacherList: [
        {
          TID: "10001",
          TName: "",
          GuideProfession: "",
          Position: "",
          Rank: "",
          Password: ""
        }
      ],
      professions: ["", ""],
      keyList: {
        SID: "",
        SName: "",
        TName: "",
        Department: "",
        Profession: "",
        Grade: "",
        ClassNumber: "",
        Password: "",
        GuideProfession: "",
        Position: "",
        Rank: ""
      }
    };
  },
  methods: {
    changeTeacher: function(index) {
      this.paneSelect = index;
      this.showInfoModal = true;
    },
    deleteTeacher: function(TID) {
      if (confirm("" + TID)) {
        this.$Message.success(TID + "");
      }
    },
    changeStudent: function(index) {
      this.paneSelect = index;
      this.showInfoModal = true;
    },
    deleteStudent: function(SID) {
      if (confirm("" + SID)) {
        this.$Message.success(SID + "");
      }
    },
    keyChange: function(key) {
      if (this.tabstatus === "Student") {
        this.newValue = this.studentList[this.paneSelect][key];
      }else if(this.tabstatus==="Teacher"){
        this.newValue = this.teacherList[this.paneSelect][key];
      }
    },
    changeOK: function() {
      this.$Message.success("");
      this.changeCancel();
    },
    changeCancel: function() {
      this.paneSelect = 0;
      this.selectKey = "";
      this.newValue = "";
    }
  }
};
</script>

<style scoped>
.row-wrap {
  margin: 2px;
}
.row {
  width: 100%;
  text-align: center;
}
.Top30 {
  margin-top: 30px;
}
h2 {
  text-align: center;
  margin-bottom: 30px;
}
</style>