<template>
    <div class="add_student">
      <h1></h1>
      <Form ref="AddStudent" :model="studentInfo" :rules="studentRule" :label-width="100" label-position="left">
        <FormItem label="" prop="SID">
          <Input v-model="studentInfo.SID" placeholder="" />
        </FormItem>
        <FormItem label="" prop="SName">
          <Input v-model="studentInfo.SName" placeholder="" />
        </FormItem>
        <FormItem label="" prop="Grade">
          <DatePicker
            @on-change="gradeChange"
            placeholder=""
            type="year"
            format="yyyy"
            :editable="false"
            style="width:200px"
          />
        </FormItem>
        <FormItem label="" prop="Profession">
          <Select @on-change="professionChange">
            <Option v-for="profession in professions" :value="profession" :key="profession"></Option>
          </Select>
        </FormItem>
        <FormItem label="" prop="ClassNumber">
          <Input v-model="studentInfo.ClassNumber" placeholder="" />
        </FormItem>
        <FormItem label="" prop="Password">
          <Input v-model="studentInfo.Password" placeholder="" />
        </FormItem>
        <div style="text-align:center">
          <Button type="success" @click="addStudent('AddStudent')" style="margin-top:30px;width:300px"></Button>
        </div>
      </Form>
    </div>
</template>

<script>
export default {
  name: "AddStudent",
  data() {
    return {
      studentInfo: {
        SID: "",
        SName: "",
        Grade: "",
        Profession: "",
        ClassNumber: "",
        Password: "123456"
      },
      studentRule: {
        SID: [
          { required: true, message: "", trigger: "blur" },
          { min: 12, max: 12, message: "", trigger: "blur" }
        ],
        SName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: "2-10",
            trigger: "blur"
          }
        ],
        Grade: [{ required: true, message: "", trigger: "blur" }],
        Profession: [
          { required: true, message: "", trigger: "blur" }
        ],
        ClassNumber: [
          { required: true, message: "", trigger: "change" }
        ],
        Password: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 6,
            max: 30,
            message: "6-30",
            trigger: "blur"
          }
        ]
      },
      professions: ["", ""]
    };
  },
  methods: {
    addStudent: function(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("");
        } else {
          this.$Message.error("");
        }
      });
    },
    gradeChange: function(value) {
      this.studentInfo.Grade = value;
    },
    professionChange: function(value) {
      this.studentInfo.Profession = value;
    }
  }
};
</script>

<style scoped>
.add_student{
  width: 300px;
  margin: 30px auto;
}
.add_student h1{
  text-align: center;
  margin-bottom: 30px;
}
</style>