<template>
  <div class="wrap">
    <div class="title">
      <h1></h1>
      <p style="color:#5cadff">20</p>
    </div>

    <Form ref="AddTopic" :model="topicInfo" :rules="topicRule">
      <FormItem label="" prop="TopicName">
        <Input v-model="topicInfo.TopicName" placeholder=""></Input>
      </FormItem>
      <FormItem label="" prop="Type">
        <Select @on-change="TypeChange" v-model="topicInfo.Type">
          <Option v-for="(value,key) in type" :value="key" :key="key">{{value}}</Option>
        </Select>
      </FormItem>
      <FormItem label="" prop="Introduction">
        <Input
          v-model="topicInfo.Introduction"
          placeholder="3-800"
          type="textarea"
          maxlength="800"
          show-word-limit
          :rows="6"
          :autosize="{minRows:6}"
        ></Input>
      </FormItem>
      <Button type="primary" long class="Top30" @click="topicSubmit('AddTopic')"></Button>
    </Form>
  </div>
</template>

<script>
export default {
  name: "AddTopic",
  data() {
    return {
      type: {},
      topicInfo: {
        TopicName: "",
        Type: "",
        Introduction: ""
      },
      topicRule: {
        TopicName: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 30,
            message: "3-30",
            trigger: "blur"
          }
        ],
        Type: [
          { required: true, message: "", trigger: "blur" }
        ],
        Introduction: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 800,
            message: "3-800",
            trigger: "blur"
          }
        ]
      }
    };
  },
  created(){
    this.type = {
      10001: "",
      10002: "",
      10003: "",
      10004: "",
      10005: ""
    };
  },
  methods: {
    TypeChange: function(value) {
      this.topicInfo.Type = value;
    },
    topicSubmit: function(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("");
        } else {
          this.$Message.error("");
        }
      });
    }
  }
};
</script>

<style scoped>
.wrap {
  width: 400px;
  margin: auto;
}
.title {
  text-align: center;
  margin: 30px;
}
.Top30 {
  margin-top: 30px;
}
</style>