<template>
  <div class="layout">
    <Header>
      <h1 class="login-title"></h1>
    </Header>
    <Content class="login-content">
      <Menu mode="horizontal" active-name="student" @on-select="getSelectValue">
        <MenuItem name="student">
          <Icon type="ios-people" />
        </MenuItem>

        <MenuItem name="teacher">
          <Icon type="md-people" />
        </MenuItem>

        <MenuItem name="admin">
          <Icon type="ios-person" />
        </MenuItem>
      </Menu>
      <Form
        :model="formData"
        label-position="left"
        :label-width="100"
        class="form"
        :rules="loginRule"
        ref="loginForm"
      >
        <FormItem label="" v-if="userType=='student'" prop="userid">
          <Input v-model="formData.userid" placeholder="" />
        </FormItem>
        <FormItem label="" v-if="userType=='teacher'" prop="userid">
          <Input v-model="formData.userid" placeholder="" />
        </FormItem>
        <FormItem label="" v-if="userType=='admin'" prop="userid">
          <Input v-model="formData.userid" placeholder="" />
        </FormItem>
        <FormItem label="" prop="password">
          <Input v-model="formData.password" placeholder="" type="password" />
        </FormItem>
        <Button type="primary" @click="submit('loginForm')" long style="margin-top:30px"></Button>
      </Form>
    </Content>
    <Footer class="footer">
      <span></span>
      <span>20188</span>
      <span></span>
    </Footer>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    const useridRule = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error(""));
      } else if (this.userType == "student" && value.length != 12) {
        return callback(new Error(""));
      } else {
        callback();
      }
    };
    const passwordRule = (rule, value, callback) => {
      if (value === "") {
        return callback(new Error(""));
      } else if (value.length < 6 || value.length > 30) {
        return callback(new Error("6-30"));
      } else {
        callback();
      }
    };
    return {
      formData: {
        userid: "",
        password: ""
      },
      userType: "student",
      loginRule: {
        userid: [{ validator: useridRule, trigger: "blur" }],
        password: [{ validator: passwordRule, trigger: "blur" }]
      }
    };
  },
  methods: {
    getSelectValue: function(name) {
      this.userType = name;
    },
    submit: function(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.$Message.success("");
          this.$router.replace({
            path: '/main'
          })
        }
      });
    }
  }
};
</script>

<style scoped>
.footer {
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 0;
}
.footer span {
  margin: 5%;
}
.login-title {
  color: white;
  text-align: center;
}
.login-content {
  width: 450px;
  margin: 50px auto;
}
.ivu-menu-item {
  margin: auto;
  width: 150px;
  text-align: center;
}
.form {
  margin: 50px auto;
  width: 350px;
}
</style>